import * as helper from './helper'

/**
 *
 */
export function ratioCompareBarChart (g, xScale, yScale, graphSize, margin, title) {
  helper.appendAxes(g)
  helper.appendBarChartLabels(g,"Vhicule", "Pourcentage de mortalit (%)")
  helper.placeTitle(g)
  setTitleText(g, title)

  positionLabels (g, graphSize.width, graphSize.height)

  yScale = d3.scaleLinear()
  .domain([0, 0.3])
  .range([graphSize.height, 0])

  xScale = d3.scaleBand()
  .domain(["Camion", "Auto"])
  .range([0, graphSize.width])

  helper.drawXAxis(g, xScale, graphSize.height)
  helper.drawYAxis(g, yScale)

  var widthRect = 250

  //Drawing the rectangles
  var pourcentageCamion = 0.2465
  g.append('rect')
  .attr("fill","red")
  .attr("x", xScale("Camion")+graphSize.width/4 - widthRect/2)
  .attr("y", yScale(pourcentageCamion))
  .attr("height", graphSize.height-yScale(pourcentageCamion))
  .attr("width", widthRect);

  var pourcentageAuto = 0.1018
  g.append('rect')
  .attr("fill","blue")
  .attr("x", xScale("Auto")+graphSize.width/4 - widthRect/2)
  .attr("y", yScale(pourcentageAuto))
  .attr("height", graphSize.height-yScale(pourcentageAuto))
  .attr("width", widthRect);
}

/**
 * @param g
 * @param width
 * @param height
 */
function positionLabels (g, width, height) {
  // TODO : Position axis labels
  var labelDate = g.selectAll('.x.axis-text')
  var labelAccident = g.selectAll('.y.axis-text')

  labelDate.attr('x', width / 2)
  labelDate.attr('y', height + 40)

  labelAccident.attr('x', -40)
  labelAccident.attr('y', height / 2)
}

/**
 * Update the title of the graph.
 *
 * @param g
 * @param title
 */
export function setTitleText (g, title) {
  // TODO : Set the title
  g.select('.title')
    .text(title)
}